import Head from 'next/head'
import Link from 'next/link'

import { PureComponent } from 'react'

import Avatar from 'react-md/lib/Avatars'
import Button from 'react-md/lib/Buttons/Button'
import FontIcon from 'react-md/lib/FontIcons'
import ListItem from 'react-md/lib/Lists/ListItem'
import NavigationDrawer from 'react-md/lib/NavigationDrawers'
import SelectField from 'react-md/lib/SelectFields'

const avatarSrc = 'https://cloud.githubusercontent.com/assets/13041/19686250/971bf7f8-9ac0-11e6-975c-188defd82df1.png'

const drawerHeaderChildren = [
  <Avatar
    style={{ alignSelf: 'center', marginLeft: 16, marginRight: 16, flexShrink: 0 }}
    key={avatarSrc} src={avatarSrc}
    role='presentation'
    iconSized
  />,
  <SelectField
    position={SelectField.Positions.BELOW}
    className='md-select-field--toolbar'
    menuItems={['Jonathan', 'Fred']}
    defaultValue='Jonathan'
    key='account-switcher'
    id='account-switcher'
  />
]

const toolbarActions = [
    <Button icon tooltipLabel='Share this page'
      tooltipDelay={150} tooltipPosition='left'
    >share</Button>
]

class NavigationLink extends PureComponent {
  render () {
    const { href, as, children, ..._props } = this.props
    return (
      <div {..._props} style={{padding: 0}}>
        <Link href={href} as={as}>
          <a className='md-list-tile md-list-tile--mini' style={{width: '100%', overflow: 'hidden'}}>
            {children}
          </a>
        </Link>
      </div>
    )
  }
}

const ReactMD = () => (
    <div>
      <Head>
        <title>React MD Page</title>
      </Head>
      <NavigationDrawer
        navItems={[
          <ListItem
            key='0'
            component={NavigationLink}
            href='/' as={ process.env.BACKEND_URL + `/`}
            leftIcon={<FontIcon>inbox</FontIcon>}
            tileClassName='md-list-tile--mini'
            primaryText={'Home'}
          />,
          <ListItem
            key='1'
            component={NavigationLink}
            href='/about' as={ process.env.BACKEND_URL + `/about`}
            leftIcon={<FontIcon>info</FontIcon>}
            tileClassName='md-list-tile--mini'
            primaryText={'About'}
          />,
          <ListItem
            key='2'
            component={NavigationLink}
            href='/tvmaze' as={ process.env.BACKEND_URL + `/tvmaze`}
            leftIcon={<FontIcon>tv</FontIcon>}
            tileClassName='md-list-tile--mini'
            primaryText={'TV Maze'}
          />
        ]}
        contentClassName='md-grid'
        drawerHeaderChildren={drawerHeaderChildren}
        mobileDrawerType={NavigationDrawer.DrawerTypes.TEMPORARY}
        tabletDrawerType={NavigationDrawer.DrawerTypes.PERSISTENT_MINI}
        desktopDrawerType={NavigationDrawer.DrawerTypes.PERSISTENT_MINI}
        toolbarTitle='Hello, World!'
        toolbarActions={toolbarActions}
      >
        <h1>My Blog</h1>
        <ul>
          {getPosts().map((post) => (
            <PostLink key={post.id} post={post}/>
          ))}
        </ul>
      </NavigationDrawer>
    </div>
)
  
export default ReactMD